.gpt3__footer {
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--color-footer);

    .gpt3__footer-heading {
        width: 100%;
        text-align: center;
        margin-bottom: 3rem;

        h1 {
            font-weight: 800;
            font-size: 62px;
            line-height: 75px;
        }
    }

    .gpt3__footer-btn {
        border: 1px solid #fff;
        padding: 1rem 2rem;
        font-size: 18px;
        margin-bottom: 10rem;
        cursor: pointer;

        transition: background-color .3s, color .3s;

        &:hover {
            background-color: #fff;
            color: var(--color-footer);
        }
    }

    .gpt3__footer-links {
        width: 100%;
        text-align: left;

        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
        flex-direction: row;

        div {
            width: 250px;
            margin: 1rem;
        }
    }

    .gpt3__footer-links_logo {
        display: flex;
        flex-direction: column;

        img {
            width: 118px;
            height: 30px;
            margin-bottom: 1rem;
        }

        p {
            font-size: 12px;
            line-height: 14px;
        }
    }

    .gpt3__footer-links_div {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;

        h4 {
            font-size: 14px;
            line-height: 16px;
            margin-bottom: 1rem;
        }

        p {
            font-size: 12px;
            line-height: 14px;
            margin: 0.5rem 0;
            cursor: pointer;
        }
    }

    .gpt3__footer-copyrights {
        margin-top: 2rem;
        text-align: center;
        width: 100%;

        p {
            font-size: 12px;
            line-height: 14px;
        }
    }
}

@media screen and (max-width: 850px) {
    .gpt3__footer {
        .gpt3__footer-heading {
            h1 {
                font-size: 44px;
                line-height: 50px;
            }
        }
    }
}

@media screen and (max-width: 550px) {
    .gpt3__footer {
        .gpt3__footer-heading {
            h1 {
                font-size: 34px;
                line-height: 42px;
            }
        }

        .gpt3__footer-btn {
            font-size: 14px;
            line-height: 20px;
        }

        .gpt3__footer-links {
            div {
                margin: 1rem 0;
            }
        }
    }
}