.gpt3__blog-container_article {
    color: #fff;

    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.gpt3__blog-container_article-image {
    height: 100%;
    width: 100%;

    img {
        width: 100%;
        height: 100%;
    }
}

.gpt3__blog-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
    background-color: var(--color-footer);

    p {
        font-weight: bold;
        font-size: 12px;
        line-height: 35px;

        &:last-child {

            cursor: pointer;
        }
    }

    h3 {
        font-weight: 800;
        font-size: 25px;
        line-height: 30px;
        margin-bottom: 5rem;
        cursor: pointer;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__blog-container_article-content {
        h3 {
            font-weight: 800;
            font-size: 18px;
            line-height: 25px;
        }
    }
}